.header {
  position: relative;
  top: 0;
  left: 0;
  width: 100%;
  height: 80px;
  z-index: 0;
  background: #3C3A47;
  font-family: 'Open Sans', sans-serif;
}
.logo {
  background: #1F65F4;
  position: relative;
  top: 0;
  left: 0;
  width: 80px;
  height: 80px;
  z-index: 20;
  display: inline-block;
}
.logo img {
    width: 28px;
    height: 39.5px;
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    margin: auto;
}
.header-body {
  position: absolute;
  top: 0;
  left: 80px;
  width: calc(100% - 80px);
  height: 100%;
  color: #fff;
}
.header-body .avatar {
    position: relative;
    top: 10px;
    left: 13px;
    border-radius: 50%;
    margin: 0px 12px;
    width: 30px;
    height: 30px;
}
.header-body p {
    display: inline-block;
    margin: 0;
    line-height: 80px;
    padding: 0 20px;
    color: #686670;
    font-size: 14px;
}
.header-body .bio-name, .header-body .rank {
    color: #FFF;
}
.header-body .challenge, .header-body .quick {
    background: #FF1782;
    height: 40px;
    top: 0;
    position: relative;
    line-height: 40px;
    border-radius: 6px;
    color: #fff;
    margin: 0 10px;
    border: 2px solid #FF1782;
}
.header-body .quick {
    background: transparent;
    border: 2px solid #FFF;
}
.header-body .cta-container {
    display: inline-block;
    float: right;
    margin-right: 30px;
    clear: both;
}

